import { Component, OnInit } from '@angular/core';
import { CompanyKantorService, DirektoriVPegawaiPosisiService } from 'src/app/sdk/hr/services';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { Kantor } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private companyKantorService: CompanyKantorService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService
    ) { }

    ngOnInit() {
        this.getData()
    }


    // sort(sort: { key: string; value: string }): void {
    //     this.sortKey = sort.key
    //     this.sortValue = sort.value
    //     this.getData()
    // }


    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''
    listOfDisplayData: Kantor[] = []
    listOfData: Kantor[] = []
    getData() {
        this.spinner.show()
        this.companyKantorService.getCompanyKantors(JSON.stringify({ sortKey: this.sortKey, sortValue: this.sortValue })).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()

                this.listOfDisplayData.forEach((kantor, indek) => {
                    this.getTotalPegawai(indek, kantor.id_kantor)
                })
            }
        )
    }

    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }



    updateFilter(value: string): void {
        this.search = value
        this.getData()
    }

    getTotalPegawai(index, id_kantor) {
        let param = {
            id_kantor: id_kantor,
            total: true
        }
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisis(JSON.stringify(param)).subscribe(
            data => {
                this.listOfDisplayData[index]['julmlah_peagwai'] = data.data[0]['total']
            }
        )
    }





    delete(id) {
        this.spinner.show();
        this.companyKantorService.deleteCompanyKantorsId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
