import { NgxSpinnerService } from 'ngx-spinner';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyDepartemenService, CompanyKantorService, CompanyPerusahaanService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { Router, ActivatedRoute } from '@angular/router';
import { Departemen, Perusahaan, Pegawai_master, Kantor } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyKantorService: CompanyKantorService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,

        private router: Router,
        private activatedRoute: ActivatedRoute,
        private companyPerusahaanService: CompanyPerusahaanService,
        private spinner: NgxSpinnerService
    ) { }

    id_kantor: any = 0;
    dep: Kantor = {};
    ngOnInit() {
        this.id_kantor = this.activatedRoute.snapshot.paramMap.get('id');

        this.form();
        this.getData()
        this.getPerusahaan()
        this.getPic()
    }

    listPerusahaan: Perusahaan[] = [];
    id_perusahaan: string = "";
    getPerusahaan() {
        this.companyPerusahaanService.getCompanyPerusahaans().subscribe(
            data => {
                this.listPerusahaan = data.data;
            }
        )
    }

    listPegawai: Pegawai_master[] = [];
    getPic() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data;
            }
        )
    }

    getData() {
        this.spinner.show()
        this.companyKantorService.getCompanyKantorsId(this.id_kantor).subscribe(
            data => {
                this.id_perusahaan = data.data.id_perusahaan;
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }


    dataForm: FormGroupTypeSafe<Kantor>;
    form() {
        this.dataForm = this.fb.group<Kantor>(
            {
                id_kantor: new FormControl(''),
                id_perusahaan: new FormControl('2'),
                updated_at: new FormControl(''),
                created_at: new FormControl(''),
                nama_kantor: new FormControl('', [Validators.required]),
                kode_kantor: new FormControl(''),
                pic_kantor: new FormControl(''),
                phone_kantor: new FormControl(''),
                alamat_kantor: new FormControl(''),
                jumlah_pegawai: new FormControl(''),
                type_kantor: new FormControl(''),
                status_kantor: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show();
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            return;
        }

        this.dep = this.dataForm.value;

        this.companyKantorService.putCompanyKantorsId(
            {
                id: this.id_kantor,
                body: this.dep
            }
        ).subscribe(
            data => {
                this.goToBack();
                this.spinner.hide();
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide();
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {

        this.router.navigate(['/hr/company/module/kantor/list'])
    }

}
