import { Component, OnInit } from '@angular/core';
import { CompanyKantorService, DirektoriPegawaiService, DirektoriVPegawaiPosisiService } from 'src/app/sdk/hr/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Pegawai, Kantor } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private companyKantorService: CompanyKantorService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }



    dataKantor: any = {}
    dataPegawai: Pegawai = {}


    id_kantor: string = ''

    ngOnInit() {
        this.id_kantor = this.routerActive.snapshot.paramMap.get('id')

        this.getKantor();

    }


    getKantor() {
        this.spinner.show()
        this.companyKantorService.getCompanyKantorsId(this.id_kantor).subscribe(
            response => {
                this.dataKantor = response.data
                this.getPegawai(response.data.pic_kantor)
                this.getTotalPegawai(response.data.id_kantor)
                this.spinner.hide()
            }
        )

    }
    getPegawai(pic_kantor: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(pic_kantor).subscribe(
            response => {
                this.dataPegawai = response.data
                this.spinner.hide()
            }
        )
    }

    goToBack() {

        this.router.navigate(['/hr/company/module/kantor/list'])
    }

    total= []
    getTotalPegawai(id_kantor) {
        let param = {
            id_kantor: id_kantor,
            total: true
        }
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisis(JSON.stringify(param)).subscribe(
            data => {
                this.dataKantor['total_pegawai'] = data.data[0]['total']
            }
        )
    }

}
